import axios from 'axios'
import qs from 'qs'

var HttpUtil = function (domType = 'loading') {
  if (!new.target) throw new Error('构造函数需要使用 new 命令调用')
  var me = this
  me.isLoadingShow = false
  me.isMsgShow = false
  me.loadingDom = createLoadingDom()
  me.msgDom = createMsgDom()

  function createLoadingDom() {
    var bgc = 'background: rgba(0, 0, 0, .6);'
    var msg = '加载中...'
    var innerStr = `<img src="data:image/webp;base64,"><div>${msg}</div>`
    var outerStr = `<div style="position: absolute;top: 50%;left: 50%;transform:translate(-50%,-50%);">${innerStr}</div>`
    var loadingDom = document.createElement('div')
    loadingDom.style = `opacity:0;transition:all .1s;${bgc}position: absolute;top: 0;left: 0;bottom: 0;right: 0; height:0; z-index: 99999;text-align: center;color: #ccc;font-size: 12px;overflow: hidden;`
    loadingDom.innerHTML = outerStr
    return window.top.document.body.appendChild(loadingDom)
  }
  me.loading = function ({ msg = '' } = {}) {
    if (msg) {
      me.loadingDom.firstChild.lastChild.innerText = msg
    }
    me.isLoadingShow ? me.loadingDom.style.height = 0 : me.loadingDom.style.height = 'auto'
    me.isLoadingShow ? me.loadingDom.style.opacity = 0 : me.loadingDom.style.opacity = 1
    me.isLoadingShow = !me.isLoadingShow
    return { isLoadingShow: me.isLoadingShow }
  }

  function createMsgDom() {
    var bgc = 'background: rgba(0, 0, 0, .6);'
    var msg = 'message'
    var innerStr = ''
    var msgDom = document.createElement('div')
    msgDom.style = `z-index:10000;min-width: 380px;border-radius: 4px;border: 1px solid #ebeef5;position: fixed;left: 50%;top: -50px;transform: translateX(-50%);background-color: #fef0f0;color:#f56c6c;transition: opacity .3s, transform .4s, top .4s;padding: 10px 20px;align-items: center;`
    msgDom.innerText = msg
    return window.top.document.body.appendChild(msgDom)
  }
  me.message = function ({ msg = '' } = {}) {
    if (msg) {
      me.msgDom.innerText = msg
    }
    if (me.isMsgShow) {
      return
    }
    me.msgDom.style.opacity = 1;
    me.msgDom.style.top = '20px';
    me.isMsgShow = true
    setTimeout(function () {
      me.msgDom.style.opacity = 0;
      me.msgDom.style.top = '-50px';
      me.isMsgShow = false
    }, 2000)
    return { isMsgShow: me.isMsgShow }
  }
}
var httpUi = new HttpUtil();

function http({
  url = '',
  method = 'get',
  timeout = 60000,
  baseURL = '',
  headers = { 'X-Requested-With': 'XMLHttpRequest' },
  data = {},
  params = {}
} = {}, {
  isLoading = true,
  loadingMsg = 'Loading...',
  dataType = 'json',
  apiSuccessCode = 0,
  apiBadHttp = 'api bad http',
  apiCodeErrMsg = 'api bad response code'
} = {}) {
  // axios 实例
  var newAxios = axios.create({
    baseURL: '',
    timeout: 60000,
  });
  // 请求拦截器
  newAxios.interceptors.request.use(function (config) {
    return config;
  }, function (error) {
    return Promise.reject(error);
  });
  // 响应拦截器
  newAxios.interceptors.response.use(function (response) {
    return response;
  }, function (error) {
    return Promise.reject(error);
  });
  return new Promise((resolve, reject) => {
    const loading = !isLoading || this.$loading({ text: 'loadingMsg' });
    newAxios.request({
        url: url,
        method: method,
        timeout: timeout,
        baseURL: baseURL,
        headers: headers,
        data: data,
        params: params,
      })
      .then(response => {
        if (apiSuccessCode !== false && response.data.code !== apiSuccessCode) {
          this.$notify.error(response.data.msg || apiCodeErrMsg);
          reject({ msg: response.data.msg || apiCodeErrMsg })
          return;
        }
        resolve({ response: response.data, data: response.data.data })
      })
      .catch(err => {
        this.$notify.error(response.data.msg || apiBadHttp);
        reject(err)
      })
      .finally(() => {
        if (isLoading) loading.close();
      })
  })
}

export { http }