	components: {},
	mounted() {
		document.getElementById("fahuo").style.display = "block";
		document.getElementById("shouhuo").style.display = "none";
		document.getElementById("faSty").style.background = "pink";
		this.$httpd({
			method: "get",
			url: this.$apis.gettingIsBP
		}).then(res => {
			this.outVals.isBP = res.data;
		});
		this.getPreData();
		// 定时获取磅读数
		this.bang.bangNo = localStorage.getItem("weighing_bang_no");
		this.getWeighingNumber();
		setInterval(() => {
			this.getWeighingNumber();
		}, this.bang.timer);
		// 序列号
		this.makeSno();
		// 限制频率请求车辆数据
		this.debouncedGetBaseInfoByCarNo = _.debounce(
			this.getBaseInfoByCarNo,
			800
		);
		// 获取历史记录
		this.list.heads = this.formOut;
		this.list.url = this.$apis.outList;
		this.getListPage();
	},
	methods: {
		preDiaTableRowClassName({ row, rowIndex }) {
			row.index = rowIndex;
		},
		filterPageListByCarNo() {
			this.getListPage(true, this.formIn.carBrand.value);
		},
		getPreData() {
			var getPreOut = () =>
				this.$axios.post(
					this.$apis.getPreData,
					this.$qs.stringify({ type: "OUT" })
				);
			var getPreIn = () =>
				this.$axios.post(
					this.$apis.getPreData,
					this.$qs.stringify({ type: "IN" })
				);
			const loading = this.$loading({ text: "信息加载中..." });
			this.$axios.all([getPreOut(), getPreIn()]).then(
				this.$axios.spread((preOut, preIn) => {
					console.log("预置信息获取完毕");
					if (preOut.data.code == 0 && preIn.data.code == 0) {
						for (let key in preIn.data) {
							preOut.data[key] = preIn.data[key];
						}
						// console.log(preOut.data)
						for (let key in this.pres) {
							this.pres[key].options = preOut.data[key];
						}
					}
					loading.close();
				})
			);
		},
		settingIsBP(val) {
			this.$httpd({
				method: "get",
				url: this.$apis.settingIsBP + Number(val)
			}).then(res => {
				console.log(res);
			});
		},
		handleShow() {
			this.show = !this.show;
			document.getElementById("text").style.border = "1px solid #ccc";
		},
		showPreDia(key, item) {
			// console.log(key, item)
			this.preDia.title = item.label;
			this.preDia.preKey = key;
			this.preDia.tableData = item.options;
			this.preDia.cols = item.cols;
			this.preDia.visible = !this.preDia.visible;
			this.show = false;
		},
		preDiaAdd(type) {
			this.preDia.curdType = type;
			if (this.preDia.curdType == "update") {
				// 补全信息
				if (!this.preDia.curRow) {
					this.$message.error("请点选需要修改的行");
					return;
				}
				var closVal = [];
				this.preDia.cols.forEach((item, index) => {
					this.preDia.cols[index].value = this.preDia.curRow[
						item.key
					];
					closVal.push({
						formatter: this.preDia.cols[index].formatter,
						key: this.preDia.cols[index].key,
						label: this.preDia.cols[index].label,
						value: this.preDia.cols[index].value
					});
				});
				console.log(closVal);
				for (let i = 0; i < closVal.length; i++) {
					this.preDia.cols.splice(i, 1, closVal[i]);
				}
			}
			this.preDia.addVisible = true;
		},
		preDiacheckRow(curRow) {
			console.log(curRow, curRow.index);
			this.preDia.curRow = curRow;
		},
		preDiaDel() {
			// 删除
			if (!this.preDia.curRow) {
				this.$message.error("请点选需要删除的行");
				return;
			}
			var apis = this.formatApi();
			this.$httpd({
				url: apis.remove,
				method: "post",
				data: this.$qs.stringify({
					ids: this.preDia.curRow.id || this.preDia.curRow.goodsid
				})
			}).then(res => {
				// if (res.code != 0) return
				var indexOfItem = this.preDia.curRow.index;
				console.log(2333, indexOfItem);
				this.preDia.tableData.splice(indexOfItem, 1);
			});
		},
		preDiaClose() {
			this.preDia.visible = false;
			this.preDia.addVisible = false;
			this.preDia.curRow = "";
			this.preDia.curdType = "";
		},
		preDiaFormAdd() {
			var apis = this.formatApi();
			var colsVal = this.formatCols();
			console.log(apis, colsVal);
			this.$httpd({
				url: this.preDia.curdType == "update" ? apis.edit : apis.add,
				method: "post",
				data: colsVal
			}).then(res => {
				if (this.preDia.curdType == "update") {
					// 更新前台
					this.preDia.cols.forEach(item => {
						this.preDia.curRow[item.key] = item.value;
					});
					this.preDia.tableData.splice(
						this.preDia.curRow.index,
						1,
						this.preDia.curRow
					);
				} else {
					res.data.index = this.preDia.tableData.length;
					this.preDia.tableData.push(res.data);
				}
			});
			this.preDia.addVisible = false;
		},
		formatApi() {
			var apiAdd = this.pres[this.preDia.preKey].apis.add;
			var apiBase = apiAdd.slice(0, apiAdd.lastIndexOf("/") + 1);
			var apis = {};
			var apiKeys = ["add", "edit", "remove"];
			apiKeys.forEach(item => {
				apis[item] = this.$apis.base + apiBase + item;
			});
			return apis;
		},
		bangNoChange() {
			this.bang.weight = 0;
			localStorage.setItem("weighing_bang_no", this.bang.bangNo);
			this.getWeighingNumber();
		},
		getWeighingNumber() {
			if (!this.bang.bangNo) {
				this.$notify({
					message: "请选择磅号",
					type: "warning",
					duration: 2000
				});
				return;
			}
			this.$httpd(
				{ url: this.$apis.getweight + "/" + this.bang.bangNo },
				{ isLoading: false }
			).then(res => {
				// console.log(res)
				this.bang.weight = res.response.weight;
			});
		},
		formatCols() {
			var cols = this.pres[this.preDia.preKey].cols;
			var colsObj = {};
			cols.forEach(item => {
				colsObj[item.key] = item.value;
			});
			if (this.preDia.curdType === "update") {
				if (this.preDia.curRow.goodsid) {
					colsObj["goodsid"] = this.preDia.curRow.goodsid;
				} else {
					colsObj["id"] = this.preDia.curRow.id;
				}
			}
			return this.$qs.stringify(colsObj);
		},
		shouShow() {
			this.formType = "IN";
			document.getElementById("shouhuo").style.display = "block";
			document.getElementById("fahuo").style.display = "none";
			document.getElementById("shouSty").style.background = "pink";
			document.getElementById("faSty").style.background = "white";
		},
		faShow() {
			this.formType = "OUT";
			document.getElementById("shouhuo").style.display = "none";
			document.getElementById("fahuo").style.display = "block";
			document.getElementById("faSty").style.background = "pink";
			document.getElementById("shouSty").style.background = "white";
		},
		makeSno() {
			// 生成序列号
			let rawDate = timeFormater(new Date()).format("YYYYMMDDHHmmssSSS");
			this.formIn.sno.value = rawDate;
			this.formOut.sno.value = rawDate;
		},
		testCarAndWeight() {
			var errMsg = "";
			if (!this.bang.weight) {
				errMsg += "磅上读数不能为零 ";
			}
			if (!this.formIn.carBrand.value) {
				errMsg += "车号不能为空 ";
			}
			return errMsg;
		},
		weightingMao() {
			if (this.testCarAndWeight()) {
				this.$message.error(this.testCarAndWeight());
				return;
			}
			this.formIn.grossWeight.value = this.bang.weight;
			this.bang.weightType = "gross_weight";
			this.$httpd({
				url: this.$apis.doWeighting,
				method: "get",
				params: {
					carNumber: this.formIn.carBrand.value,
					weightType: this.bang.weightType // car_weight
				}
			}).then(res => {
				this.formIn.carWeight.value = res.data.carWeight;
				if (res.data.sno) this.formIn.sno.value = res.data.sno;
				if (res.data.sendNetweight)
					this.formIn.sendNetweight.value = res.data.sendNetweight;
				if (res.data.id) this.inVals.saveId = res.data.id;
			});
		},
		weightingPi() {
			if (this.testCarAndWeight()) {
				this.$message.error(this.testCarAndWeight());
				return;
			}
			this.formIn.carWeight.value = this.bang.weight;
			this.bang.weightType = "car_weight";
			this.$httpd({
				url: this.$apis.doWeighting,
				method: "get",
				params: {
					carNumber: this.formIn.carBrand.value,
					weightType: this.bang.weightType // car_weight
				}
			}).then(res => {
				this.formIn.grossWeight.value = res.data.grossWeight;
				if (res.data.sno) this.formIn.sno.value = res.data.sno;
				if (res.data.sendNetweight)
					this.formIn.sendNetweight.value = res.data.sendNetweight;
				if (res.data.id) this.inVals.saveId = res.data.id;
			});
		},
		saver() {
			var saveInfos = {};
			for (let key in this.formIn) {
				saveInfos[key] = this.formIn[key].value;
			}
			saveInfos["id"] = this.inVals.saveId;
			if (!this.inVals.bulu) {
				saveInfos["weightType"] = this.bang.weightType;
			}
			console.log(saveInfos);
			this.$httpd({
				url: this.$apis.saveReceiveWeight,
				method: "post",
				data: this.$qs.stringify(saveInfos)
			}).then(res => {
				console.log("保存::", res);
				this.getPreData();
				this.getListPage(true);
			});
		},
		printr(order) {
			this.saver();

			if (!order) {
				var order = {};
				for (let key in this.formIn) {
					order[key] = this.formIn[key].value
						? this.formIn[key].value
						: "";
				}
			}
			var orderHtml = `<div class="main">
          <div class="a">项城市鸿睿砼业入库单</div>
          <div class="b com-a">
            <span class="b-a">流水号: ${order.sno}</span>
            <span class="b-a">打印时间: ${timeFormater(new Date()).format(
				"YYYY年MM月DD日HH时mm分ss秒"
			)}</span>
            <span class="b-a">计量单位: t</span>
          </div>
          <table class="c">
            <tr>
              <td class="c-a">汽车牌号</td>
              <td class="c-c">${
					order.carBrand == null ? "" : order.carBrand
				}</td>
              <td class="c-a">毛重</td>
              <td class="c-c">${
					order.grossWeight == null ? 0.0 : order.grossWeight
				}</td>
            </tr>
            <tr>
              <td class="c-a">货物名称</td>
              <td class="c-c">${
					order.goodsName == null ? "" : order.goodsName
				}</td>
              <td class="c-a">皮重</td>
              <td class="c-c">${
					order.carWeight == null ? 0.0 : order.carWeight
				}</td>
            </tr>
            <tr>
              <td class="c-a">收货单位</td>
              <td class="c-c">${
					order.receiveDept == null ? "" : order.receiveDept
				}</td>
              <td class="c-a">净重</td>
              <td class="c-c">${
					order.netWeight == null ? 0.0 : order.netWeight
				}</td>
            </tr>
            <tr>
              <td class="c-a">单价</td>
              <td class="c-c">${
					order.price == null ? 0.0 : order.price
				} <span class="c-b">元</span></td>
              <td class="c-a">结算净重</td>
              <td class="c-c">${
					order.settleNetweight == null ? 0.0 : order.settleNetweight
				}</td>
            </tr>
            <tr>
              <td class="c-a">金额</td>
              <td class="c-c">${
					order.totalFee == null ? 0.0 : order.totalFee
				} <span class="c-b">元</span></td>
              <td class="c-a">备注</td>
              <td class="c-c">${order.remark == null ? "" : order.remark}</td>
            </tr>
          </table>
          <div class="d com-a">
            <span class="d-a">司磅员: ${
				order.bangyuan == null ? " " : order.bangyuan
			}</span>
            <span class="d-a">驾驶员: </span>
            <span class="d-a">收款员: </span>
          </div>
        </div>`;
			this.printHtml(orderHtml);
		},
		printHtml(orderHtml) {
			var styles =
				".main {width: 190mm;margin: 0 auto;background: transparent;font-size: 16px;}table {table-layout: fixed;border-collapse: collapse;border-spacing: 0px;width: 100%;}td {word-break: keep-all;overflow: hidden;white-space: nowrap;padding: 5px;border: 1px solid #000;}.com-a {display: flex;justify-content: space-between;font-size: 14px;margin: 4px 10px;}.a {font-size: 24px;text-align: center;margin: 10px 0 5px;}.a--c {font-size: 20px;text-align: center;margin: 5px 0 10px;}.c-a {width: 12%;}.c-c {width: 38%;}.c-b {float: right;}.m {font-size: 24px;text-align: center;margin: 10px 0 5px;}.o td {text-align: center;}.p {margin-top: 8mm;}.d-a {margin-top: 12px;flex: 1;}";
			var domStr = `
      <!DOCTYPE html>
      <html lang="zh-cn">
      <head>
        <meta charset="UTF-8">
        <meta http-equiv="X-UA-Compatible" content="IE=edge,chrome=1">
        <title></title>
        <style>${styles}</style>
      </head>
      <body>${orderHtml}</body>
      </html>`;
			let LODOP = getLodop();
			LODOP.PRINT_INIT("");
			LODOP.SET_PRINT_PAGESIZE(1, "241mm", "130mm", "CreateCustomPage");
			LODOP.ADD_PRINT_HTM("2%", "7mm", "200mm", "96%", domStr);
			if (this.$apis.isDev) {
				LODOP.PRINT_DESIGN();
				// LODOP.PREVIEW();
			} else {
				LODOP.PRINT();
			}
		},
		refreshr() {
			// 刷新
			for (let key in this.formIn) {
				this.formIn[key].value = "";
			}
			this.bang.weightType = "";
			this.makeSno();
			this.inVals.bulu = false;
			this.inVals.saveId = "";
			this.getListPage(true);
			this.getPreData();
		},
		budar() {
			// 补打
			var order = {};
			this.printr(this.list.lastActiveRow);
		},
		bulur() {
			// 补录
			// console.log(this.inVals.bulu)
			this.makeSno();
			this.formIn.grossWeight.disabled = !this.inVals.bulu;
			this.formIn.carWeight.disabled = !this.inVals.bulu;
		},
		getBaseInfoByCarNo() {
			if (!this.autoGetBaseinfoByCarNo) {
				return;
			}
			this.$httpd({
				url: this.$apis.getlogbycarNo,
				method: "get",
				params: { carNumber: this.formIn.carBrand.value }
			}).then(res => {
				// 填充基本信息
				// this.fillingBaseInfo(res.data)
				var originInfos = res.data;
				for (let key in this.formIn) {
					if (this.formIn[key].isBase && originInfos[key]) {
						this.formIn[key].value = originInfos[key];
					}
				}
			});
		},
		computedNetWeight() {
			// 净重
			if (
				this.formIn.carWeight.value &&
				this.formIn.grossWeight.value &&
				this.formIn.grossWeight.value - this.formIn.carWeight.value >= 0
			) {
				this.formIn.netWeight.value = (
					this.formIn.grossWeight.value - this.formIn.carWeight.value
				).toFixed(2);
			} else {
				this.formIn.netWeight.value = "";
			}
		},
		computedJiesuanNetWeight() {
			// 结算净重
			this.formIn.takeOff.value = (
				(this.formIn.netWeight.value * this.formIn.takeoffRate.value) /
				100
			).toFixed(2);
			this.formIn.settleNetweight.value = (
				this.formIn.netWeight.value - this.formIn.takeOff.value
			).toFixed(2);
		},
		computedAmount() {
			// 总金额
			if (this.formIn.settleNetweight.value) {
				var amount =
					this.formIn.settleNetweight.value * this.formIn.price.value;
				this.formIn.totalFee.value =
					amount >= 0 ? amount.toFixed(2) : "";
			} else {
				this.formIn.totalFee.value = "";
			}
		},
		receiveList() {
			// 历史记录
			this.$httpd({
				url: this.$apis.receiveList,
				method: "post"
			});
		},
		getListPage: function(isInit = false, carNumber = "") {
			if (isInit === true) {
				this.list.page.pageNum = 1;
				this.list.page.pageSize = 5;
				this.list.page.totalSize = 0;
				this.list.page.carNumber = carNumber;
			}
			var This = this;
			this.$httpd({
				method: "post",
				url: this.list.url,
				data: this.$qs.stringify({
					pageNum: this.list.page.pageNum,
					pageSize: this.list.page.pageSize,
					carBrand: this.list.page.carNumber,
					orderByColumn: this.list.page.orderByColumn,
					isAsc: this.list.page.isAsc
				})
			}).then(res => {
				var rows = res.response.rows;
				this.list.page.totalSize = res.response.total;
				this.list.rows = rows;
			});
		},
		handlePageChange: function(page) {
			this.list.page.pageNum = page;
			this.getListPage();
		},
		handleActiveRow: function(row) {
			this.list.lastActiveRow = row;
			// 填充基本信息
			if (this.formType === "IN") {
				this.autoGetBaseinfoByCarNo = false;
				this.fillingBaseInfo(row, "carBrand");
				setTimeout(() => {
					this.autoGetBaseinfoByCarNo = true;
				}, 1000);
			} else {
				this.fillingBaseInfoOut(row, ["carCnt"]);
			}
		},
		fillingBaseInfo: function(originInfos, carNumber = false) {
			if (carNumber) {
				this.formIn[carNumber].value = originInfos[carNumber] || "";
			}
			for (let key in this.formIn) {
				if (this.formIn[key].isBase) {
					this.formIn[key].value = originInfos[key] || "";
				}
			}
		},
		saveOut() {
			var saveInfos = {};
			for (let key in this.formOut) {
				saveInfos[key] = this.formOut[key].value;
			}
			saveInfos["taskId"] = this.formOut.id.value;
			saveInfos["carId"] = this.outVals.carId;
			// if (!this.inVals.bulu) {
			//   saveInfos['weightType'] = this.bang.weightType
			// }
			this.$httpd({
				url: this.$apis.saveOutWeight,
				method: "post",
				data: this.$qs.stringify(saveInfos)
			}).then(res => {
				console.log("保存::", res);
				this.getListPage(true);
			});
			this.getPreData();
		},
		printOut(order) {
			this.saveOut();
			if (!order) {
				var order = {};
				for (let key in this.formOut) {
					order[key] = this.formOut[key].value
						? this.formOut[key].value
						: "";
				}
			}
			for (let key in order) {
				order[key] = order[key] ? order[key] : "";
			}
			// console.log(2333, order)
			var orderHtml = `<div class="main">
          <div class="a">项城市鸿睿砼业有限公司称重计量单</div>
          <div class="a a--c">砼发货单</div>
          <div class="b com-a">
            <span class="b-a">序号: ${order.sno}</span>
            <span class="b-a">时间: ${timeFormater(new Date()).format(
				"YYYY年MM月DD日HH时mm分ss秒"
			)}</span>
            <span class="b-a">计量单位: 吨</span>
          </div>
          <table class="o">
            <tr>
              <td>收货单位</td>
              <td colspan="3">${
					order.receiver == null ? "" : order.receiver
				}</td>
              <td>强度等级</td>
              <td>${order.productKind == null ? "" : order.productKind}</td>
              <td>浇筑方式</td>
              <td>${order.waterMethod == null ? "" : order.waterMethod}</td>
            </tr>
            <tr>
              <td>工程名称</td>
              <td colspan="3">${
					order.taskName == null ? "" : order.taskName
				}</td>
              <td>坍落度</td>
              <td>${order.tanluodu == null ? "" : order.tanluodu}</td>
              <td>抗渗等级</td>
              <td>${order.kslevel == null ? "" : order.kslevel}</td>
            </tr>
            <tr>
              <td>工程部位</td>
              <td colspan="3">${
					order.waterPart == null ? "" : order.waterPart
				}</td>
              <td>现场坍落度</td>
              <td></td>
              <td>到达时间</td>
              <td></td>
            </tr>
            <tr>
              <td>车号</td>
              <td>${order.carBrand == null ? "" : order.carBrand}</td>
              <td>司机</td>
              <td>${order.driverMobile == null ? "" : order.driverMobile}</td>
              <td>车次</td>
              <td>${order.carCnt == null ? 0 : order.carCnt}</td>
              <td>本车方数</td>
              <td>${
					order.equalFangliang == null ? 0 : order.equalFangliang
				}</td>
            </tr>
            <tr>
              <td>毛重</td>
              <td>${order.grossWeight == null ? 0.0 : order.grossWeight}</td>
              <td>皮重</td>
              <td>${order.carWeight == null ? 0.0 : order.carWeight}</td>
              <td>净重</td>
              <td>${order.netWeight == null ? 0.0 : order.netWeight}</td>
              <td>累计方量</td>
              <td>${order.ljfangliang == null ? "" : order.ljfangliang}</td>
            </tr>
            <tr>
              <td>操作员</td>
              <td>${order.operator == null ? "" : order.operator}</td>
              <td>质检员</td>
              <td></td>
              <td>监磅人</td>
              <td></td>
              <td rowspan="2">现场<br>签收</td>
              <td rowspan="2"></td>
            </tr>
            <tr>
              <td>备注</td>
              <td colspan="5">${order.remark == null ? "" : order.remark}</td>
            </tr>
            <tr>
              <td style="font-size:12px;" colspan="8">免责条款：送达现场的砼施工方不按规范及时浇注，不按GB/50204标准7.4.7要求进行养护或加水造成的后果本公司概不负责。</td>
            </tr>
          </table>
          <div class="d com-a">
            <span>白联: 存根</span>
            <span>粉联: 工地</span>
            <span>蓝联: 记账</span>
            <span>黄联: 结账</span>
          </div>
        </div>`;
			this.printHtml(orderHtml);
		},
		refreshOut() {
			for (let key in this.formOut) {
				this.formOut[key].value = "";
			}
			this.makeSno();
			this.getPreData();
			this.outVals.bulu = false;
			// this.getListPage(true)
		},
		updateOutCarList() {
			// 更新 outCarList
		},
		budaOut() {
			this.makeSno();
			var order = this.list.lastActiveRow;
			// order['ljfangliang'] = order['ljfangliang']
			this.printOut(order);
		},
		buluOut() {
			this.makeSno();
			this.formOut.grossWeight.disabled = !this.outVals.bulu;
			this.formOut.carWeight.disabled = !this.outVals.bulu;
		},
		weightingOut(type) {
			if (type === "pi") {
				this.formOut.carWeight.value = this.bang.weight;
			} else {
				this.formOut.grossWeight.value = this.bang.weight;
			}
		},
		fillingBaseInfoByTaskName() {
			// this.formOut.taskName.value
			// this.pres.tlist.options
			var infos = this.pres.tlist.options.find(
				item => item.name === this.formOut.taskName.value
			);
			console.log(infos);
			if (infos) {
				infos["taskName"] = infos["name"];
				infos["carCnt"] = infos["carCnt"]
					? Number(infos["carCnt"]) + 1
					: 1;
				infos["tanluodu"] = infos["locationTanluodu"];
				for (let key in this.formOut) {
					if (this.formOut[key].isBase) {
						if (infos[key]) this.formOut[key].value = infos[key];
					}
				}
			}
		},
		fillingBaseInfoOut(originInfos, exclude = []) {
			for (let key in this.formOut) {
				if (this.formOut[key].isBase && exclude.indexOf(key) === -1) {
					this.formOut[key].value = originInfos[key] || "";
				}
			}
		},
		computedOutNetweight() {
			if (
				this.formOut.carWeight.value &&
				this.formOut.grossWeight.value &&
				this.formOut.grossWeight.value - this.formOut.carWeight.value >=
					0
			) {
				this.formOut.netWeight.value = (
					this.formOut.grossWeight.value -
					this.formOut.carWeight.value
				).toFixed(2);
			} else {
				this.formOut.netWeight.value = "";
			}
		},
		computedOutFangshu() {
			// if (this.formOut.productKind.value && this.formOut.netWeight.value) {
			var item = this.pres.comList.options.find(
				item => item.strengthNo === this.formOut.productKind.value
			);
			if (item && item.ratio) {
				this.formOut.equalFangliang.value = (
					this.formOut.netWeight.value / item.ratio
				).toFixed(2);
			} else {
				this.formOut.equalFangliang.value = "";
			}
			// }
		}
	},
	watch: {
		formType: function(val, oldVal) {
			if (val === "IN") {
				console.log("收货");
				this.list.heads = this.formIn;
				this.list.url = this.$apis.receiveList;
				this.refreshOut();
			} else {
				console.log("发货");
				this.list.heads = this.formOut;
				this.list.url = this.$apis.outList;
				this.refreshr();
			}
		},
		"formIn.carBrand.value": function(val, oldVal) {
			console.log(val, oldVal);
			if (val) {
				this.debouncedGetBaseInfoByCarNo();
			}
		},
		"formIn.grossWeight.value": function(val) {
			// 净重
			this.computedNetWeight();
		},
		"formIn.carWeight.value": function(val) {
			// 净重
			this.computedNetWeight();
		},
		"formIn.netWeight.value": function(val) {
			// 结算净重
			this.computedJiesuanNetWeight();
		},
		"formIn.takeoffRate.value": function(val) {
			// 结算净重
			this.computedJiesuanNetWeight();
		},
		"formIn.price.value": function(val) {
			// 金额
			this.computedAmount();
		},
		"formOut.carBrand.value": function(val) {
			// 车号->皮重 , 车号->车编号,  车号->更新 pres.outCarList
			if (val) {
				var item = this.pres.outCarList.options.find(
					item => item.carNum === val
				);
				this.formOut.carWeight.value = item.carWeight || "";
				this.formOut.carNo.value = item.carNo || "";
				this.outVals.carId = item.id || "";
			} else {
				this.formOut.carWeight.value = "";
				this.formOut.carNo.value = "";
				this.outVals.carId = "";
			}
		},
		"formOut.grossWeight.value": function(val) {
			// 净重
			this.computedOutNetweight();
		},
		"formOut.carWeight.value": function(val) {
			// 净重
			this.computedOutNetweight();
		},
		"formOut.netWeight.value": function(val) {
			// 方数
			this.computedOutFangshu();
		},
		"formOut.productKind.value": function(val) {
			// 方数
			this.computedOutFangshu();
		},
		"formOut.equalFangliang.value": function(val, oldVal) {
			// 累计方数
			this.formOut.ljfangliang.value = (
				Number(this.formOut.ljfangliang.value) +
				(val - oldVal)
			).toFixed(2);
		}
		// 'formOut.carCnt.value': function (val) { // 累计车次
		//   // this.formOut.carCnt.value = 1 + Number(val)
		// }
	},
	computed: {
		// 'computedCarCnt': {
		//   get: function(){
		//     return 1 + Number(this.formOut.carCnt.value)
		//   },
		//   set: function(newVal){
		//     this.formOut.carCnt.value =
		//   }
		// }
	}
};
</script>
<style lang="css">
/*.weightable .el-table .cell,
.weightable .el-table th div {
  padding-right: 5px;
  overflow: inherit;
  text-overflow: inherit;
}*/
