	margin: 10px;
}
.weightable .el-table__body-wrapper tr td .cell {
	text-overflow: inherit;
	overflow: inherit;
}
.weightable {
	flex: 1;
	background: #fff;
	overflow-y: hidden;
}
.weightable .el-table td,
.weightable .el-table th {
	padding: 0;
}
.weightable tr {
