import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './plugins/element.js'

import { http } from './common/js/http.js'
Vue.prototype.$httpd = http

import axios from 'axios'
Vue.prototype.$axios = axios

import qs from 'qs'
Vue.prototype.$qs = qs

import AFTableColumn from 'af-table-column'
Vue.use(AFTableColumn)

// const apiBase = 'http://192.168.0.166:9088'
// const apiBase = 'http://192.168.0.128:9088'
const isDev = false
const apiBase = isDev ? 'http://192.168.0.166:6600' : ''
const apis = {
  isDev: isDev,
  base: apiBase,
  getPreData: apiBase + '/duties/doweight/getPreData',
  getweight: apiBase + '/duties/doweight/getWeight',
  doWeighting: apiBase + '/duties/doweight/doWeighting', // 【收货】时点击称皮还是称重
  saveReceiveWeight: apiBase + '/duties/doweight/saveReceiveWeight', // 保存收货称重
  getlogbycarNo: apiBase + '/duties/doweight/getlogbycarNo', // 根据车牌号获取最近一次记录
  receiveList: apiBase + '/stock/receiveStock/list',
  saveOutWeight: apiBase + '/duties/doweight/saveOutWeight', // 出货单,保存
  outList: apiBase + '/duties/tasksCars/list', // 出货单,list
  gettingIsBP: apiBase + '/duties/doweight/gettingIsBP', // 是否补票
  settingIsBP: apiBase + '/duties/doweight/settingIsBP/', // {isBP}   2.设置【补票数据】开关0：关，1开
}
Vue.prototype.$apis = apis

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')